import React from 'react';
import { Card, Button, Badge } from 'react-bootstrap';
import { useAuth } from '../context/AuthContext';

const JobCard = ({ item, onSwipe, isCandidate = true }) => {
  const { user } = useAuth();

  const renderContent = () => {
    if (isCandidate) {
      // Affichage d'une offre d'emploi
      return (
        <>
          <Card.Title>{item.title}</Card.Title>
          <Card.Subtitle className="mb-2 text-muted">{item.company}</Card.Subtitle>
          <Card.Text>
            <strong>Description:</strong> {item.description}<br />
            <strong>Localisation:</strong> {item.location}<br />
            <strong>Salaire:</strong> {item.salary}€<br />
            <strong>Type de contrat:</strong> {item.type}
          </Card.Text>
          {item.requirements && (
            <div className="mb-3">
              <strong>Exigences:</strong>
              <Card.Text>{item.requirements}</Card.Text>
            </div>
          )}
          {item.benefits && (
            <div className="mb-3">
              <strong>Avantages:</strong>
              <Card.Text>{item.benefits}</Card.Text>
            </div>
          )}
        </>
      );
    } else {
      // Affichage d'un candidat
      return (
        <>
          <Card.Title>{item.firstName} {item.lastName}</Card.Title>
          <Card.Subtitle className="mb-2 text-muted">
            {item.position || 'Candidat'}
          </Card.Subtitle>
          
          {/* Afficher l'offre pour laquelle le candidat a liké */}
          {item.job_title && (
            <div className="mb-3 p-3 bg-light rounded">
              <strong>Offre likée :</strong> {item.job_title}<br />
              <strong>Entreprise :</strong> {item.job_company}
            </div>
          )}
          
          {item.bio && (
            <Card.Text>
              <strong>Bio:</strong> {item.bio}<br />
            </Card.Text>
          )}
          {item.location && (
            <Card.Text>
              <strong>Localisation:</strong> {item.location}<br />
            </Card.Text>
          )}
          {item.company && (
            <Card.Text>
              <strong>Entreprise actuelle:</strong> {item.company}<br />
            </Card.Text>
          )}
          {item.skills && item.skills.length > 0 && (
            <div className="mb-3">
              <strong>Compétences:</strong>
              <div className="mt-2">
                {item.skills.map((skill, index) => (
                  <Badge key={index} bg="primary" className="me-2">
                    {skill}
                  </Badge>
                ))}
              </div>
            </div>
          )}
        </>
      );
    }
  };

  return (
    <Card className="job-card">
      <Card.Body>
        {renderContent()}
        <div className="d-flex justify-content-between mt-3">
          <Button 
            variant="danger" 
            onClick={() => onSwipe('dislike')}
          >
            Dislike
          </Button>
          <Button 
            variant="success" 
            onClick={() => onSwipe('like')}
          >
            Like
          </Button>
        </div>
      </Card.Body>
    </Card>
  );
};

export default JobCard; 