import React from 'react';
import { useAuth } from '../context/AuthContext';
import ProfileForm from '../components/ProfileForm';
import RecruiterProfileForm from '../components/RecruiterProfileForm';

const Profile = () => {
    const { user } = useAuth();

    // Mapping des valeurs du secteur d'activité
    const industryMapping = {
        'tech': 'Technologie',
        'finance': 'Finance',
        'healthcare': 'Santé',
        'education': 'Éducation',
        'retail': 'Commerce',
        'manufacturing': 'Industrie',
        'other': 'Autre'
    };

    // Fonction pour obtenir la valeur d'affichage du secteur
    const getIndustryDisplayValue = (value) => {
        return industryMapping[value] || value;
    };

    if (!user) {
        return (
            <div className="min-h-screen bg-gray-100 py-12 px-4 sm:px-6 lg:px-8">
                <div className="max-w-3xl mx-auto">
                    <div className="text-center">
                        <h2 className="text-2xl font-bold text-gray-900">Chargement...</h2>
                    </div>
                </div>
            </div>
        );
    }

    return (
        <div className="min-h-screen bg-gray-100 py-12 px-4 sm:px-6 lg:px-8">
            <div className="max-w-3xl mx-auto">
                <div className="bg-white shadow rounded-lg p-6 mb-8">
                    <div className="flex items-center space-x-4 mb-6">
                        {user.profilePicture ? (
                            <img
                                src={`http://localhost/TinderWork/backend/${user.profilePicture}`}
                                alt="Profile"
                                className="w-20 h-20 rounded-full object-cover"
                            />
                        ) : (
                            <div className="w-20 h-20 rounded-full bg-gray-200 flex items-center justify-center">
                                <span className="text-2xl text-gray-500">
                                    {user.firstName?.[0]}{user.lastName?.[0]}
                                </span>
                            </div>
                        )}
                        <div>
                            <h2 className="text-2xl font-bold text-gray-900">
                                {user.firstName} {user.lastName}
                            </h2>
                            <p className="text-gray-600">{user.email}</p>
                            <p className="text-sm text-gray-500 capitalize">{user.role}</p>
                        </div>
                    </div>

                    {user.role === 'recruiter' ? (
                        <>
                            <div className="border-t border-gray-200 pt-6">
                                <h3 className="text-lg font-medium text-gray-900 mb-4">Informations de l'entreprise</h3>
                                <div className="grid grid-cols-1 gap-4">
                                    {user.companyName && (
                                        <div>
                                            <p className="text-sm font-medium text-gray-500">Nom de l'entreprise</p>
                                            <p className="mt-1">{user.companyName}</p>
                                        </div>
                                    )}
                                    {user.companyDescription && (
                                        <div>
                                            <p className="text-sm font-medium text-gray-500">Description</p>
                                            <p className="mt-1">{user.companyDescription}</p>
                                        </div>
                                    )}
                                    {user.companyLocation && (
                                        <div>
                                            <p className="text-sm font-medium text-gray-500">Localisation</p>
                                            <p className="mt-1">{user.companyLocation}</p>
                                        </div>
                                    )}
                                    {user.companyWebsite && (
                                        <div>
                                            <p className="text-sm font-medium text-gray-500">Site web</p>
                                            <a href={user.companyWebsite} target="_blank" rel="noopener noreferrer" className="text-blue-600 hover:text-blue-800">
                                                {user.companyWebsite}
                                            </a>
                                        </div>
                                    )}
                                    {user.companySize && (
                                        <div>
                                            <p className="text-sm font-medium text-gray-500">Taille de l'entreprise</p>
                                            <p className="mt-1">{user.companySize}</p>
                                        </div>
                                    )}
                                    {user.industry && (
                                        <div>
                                            <p className="text-sm font-medium text-gray-500">Secteur d'activité</p>
                                            <p className="mt-1">{getIndustryDisplayValue(user.industry)}</p>
                                        </div>
                                    )}
                                </div>
                            </div>
                            <div className="mt-8">
                                <RecruiterProfileForm />
                            </div>
                        </>
                    ) : (
                        <>
                            <div className="border-t border-gray-200 pt-6">
                                <h3 className="text-lg font-medium text-gray-900 mb-4">Informations personnelles</h3>
                                <div className="grid grid-cols-1 gap-4">
                                    {user.bio && (
                                        <div>
                                            <p className="text-sm font-medium text-gray-500">Bio</p>
                                            <p className="mt-1">{user.bio}</p>
                                        </div>
                                    )}
                                    {user.location && (
                                        <div>
                                            <p className="text-sm font-medium text-gray-500">Localisation</p>
                                            <p className="mt-1">{user.location}</p>
                                        </div>
                                    )}
                                    {user.position && (
                                        <div>
                                            <p className="text-sm font-medium text-gray-500">Poste actuel</p>
                                            <p className="mt-1 capitalize">{user.position}</p>
                                        </div>
                                    )}
                                </div>
                            </div>

                            <div className="border-t border-gray-200 pt-6 mt-6">
                                <h3 className="text-lg font-medium text-gray-900 mb-4">Compétences</h3>
                                <div className="flex flex-wrap gap-2">
                                    {user.skills && user.skills.length > 0 ? (
                                        user.skills.map(skill => (
                                            <span
                                                key={skill.id}
                                                className="bg-blue-100 text-blue-800 px-3 py-1 rounded-full text-sm"
                                            >
                                                {skill.name}
                                            </span>
                                        ))
                                    ) : (
                                        <p className="text-gray-600">Aucune compétence spécifiée</p>
                                    )}
                                </div>
                            </div>

                            <div className="mt-8">
                                <ProfileForm />
                            </div>
                        </>
                    )}
                </div>
            </div>
        </div>
    );
};

export default Profile; 